import React from 'react';
import Dimensions from 'react-dimensions';
import {Table, Column, Cell} from 'fixed-data-table-2';
import {TextCell, ServiceLinkCell} from '../../../Common/Table/Cells';
import PropTypes from 'prop-types';
import "../../../Table/index.css";


class TableSection extends React.Component {

    render(){
        const {containerWidth, containerHeight, data} = this.props;

        return (
            <Table
                className="table__wrapper"
                rowsCount={data.length}
                rowHeight={45}
                headerHeight={35}
                width={containerWidth}
                height={containerHeight}>

                <Column
                    columnKey="name"
                    header={<Cell className="table__header">Service Name</Cell>}
                    cell={<ServiceLinkCell className="table__cell" data={data}/>}
                    width={200}
                />
                <Column
                    columnKey="display"
                    header={<Cell className="table__header">Service Display Name</Cell>}
                    cell={<TextCell className="table__cell" data={data}/>}
                    width={250}
                />
                <Column
                    columnKey="path"
                    header={<Cell className="table__header">Service Executable Full Path</Cell>}
                    cell={<TextCell className="table__cell" data={data}/>}
                    width={350}
                />

            </Table>
        );
    }

    static propTypes = {
        data: PropTypes.array.isRequired,
    };
}


export default Dimensions()(TableSection);


